<template>
  <div class="global-query">
    <!-- 搜索固定标题行 -->
    <div ref="queryBox" class="queryBox" @click="show=!show">
      <span class="search-title" v-show="queryData.length===0">查询条件</span>
      <span class="search-title1" v-show="queryData.length > 0">查询条件</span>
      <div class="search-item">
          <span class="query" v-for="(item, index) in queryData" :key="index">
            <strong>{{item.title}}</strong>
            <span>
              {{item.label}}
            </span>
          </span>
      </div>
      <div style="width: 100px;float: right;">
        <Button type="primary" size="small" icon="ios-arrow-dropdown">查询条件</Button>
      </div>
    </div>

    <!-- 搜索查询条件下拉框 -->
    <div ref="queryDown" class="queryDown" v-show="show">
      <Form ref="formInline" inline :label-width="100" :model="formInline">
        <FormItem v-for="(item, index) in formArr" :key="index" :label="item.title + '：'">
          <!-- type：1  文本框-->
          <Input v-if="item.type === 1" type="text" class="queryInput" v-model="formInline[item.field]" :placeholder="item.title" style="width: 200px;"></Input>
          <!-- type：2 选择框 -->
          <Select filterable clearable  v-if="item.type === 2" v-model="formInline[item.field]" style="width:200px">
            <Option v-for="(it, ind) in item.data" :value="it.value" :key="ind">{{ it.label }}</Option>
          </Select>
          <!-- type：3 日期选择框 -->
          <DatePicker v-if="item.type === 3" size="large" type="year" v-model="formInline[item.field]" :start-date="new Date()" placement="bottom-end" :placeholder="item.title" style="width: 200px"></DatePicker>
          <!-- type：4 起止日期选择框 -->
          <DatePicker v-if="item.type === 4" type="daterange" v-model="formInline[item.field]" :start-date="new Date()" placement="bottom-end" placeholder="请输入起止日期" style="width: 200px" on-open-change="true"></DatePicker>
        </FormItem>
        <FormItem :label-width="0" style="float: right">
          <Button type="primary" size="small" @click.stop="query"><i class="iconfont icon-chaxun1"></i>查询</Button>
          <Button type="primary" size="small" @click.stop="cleardata" style="margin-left: 15px"><i class="iconfont icon-zhongzhi"></i>重置</Button>
        </FormItem>
      </Form>
    </div>
  </div>
</template>
<script>
import { renderYearTime, renderTime } from '@/utils/common'
export default {
  name: 'global-query',
  data () {
    return {
      formInline: {}, // 查询表单值
      show: false // 查询弹出框状态控制
    }
  },
  computed: {
    // 查询条件返回数组
    queryData: function () {
      let arr = []
      for (let i in this.formInline) {
        // 当this.formInline[i] === ‘系统管理-》登录管理中的起止时间这个字段时’，因为会自动触发change,所以初始化删除
        if (i === 'startAndEndTime') {
          if (this.formInline[i][0] === '' && this.formInline[i][1] === '') {
            delete this.formInline[i]
          }
        }
        if (this.formInline[i] !== '' && this.formInline[i] !== undefined) {
          /* 获取展示值label */
          let label
          let fromArrObj = this.formArr.filter(item => item.field === i)[0]
          if (fromArrObj.type === 2) {
            label = fromArrObj.data.filter(item => item.value === this.formInline[i])[0].label
          } else if (fromArrObj.type === 3) {
            label = renderYearTime(this.formInline[i])
          } else if (fromArrObj.type === 4) {
            label = renderTime(this.formInline[i][0]) + ' - ' + renderTime(this.formInline[i][1])
          } else {
            label = this.formInline[i]
          }
          let obj = {
            title: this.formArr.filter(item => item.field === i)[0].title,
            field: i,
            value: this.formInline[i],
            label: label
          }
          arr.push(obj)
        }
      }
      return arr
    }
  },
  props: {
    formArr: {
      type: Array,
      default: function () {
        return []
      }
    }
  },
  mounted () {
    document.addEventListener('click', this.queryHide)
  },
  beforeDestroy () {
    document.removeEventListener('click', this.queryHide)
  },
  methods: {
    /* 清空查询数据 */
    cleardata () {
      this.formInline = {}
      this.query()
    },
    /* 点击查询 */
    query () {
      this.show = false
      let obj = {}
      for (let i in this.queryData) {
        if (this.queryData[i].field === 'cl010') {
          obj[this.queryData[i].field] = renderYearTime(this.queryData[i].value)
        } else {
          obj[this.queryData[i].field] = this.queryData[i].value
        }
      }
      this.$emit('query', obj)
    },
    /* 点击任意地方隐藏搜索栏 */
    queryHide (e) {
      if ((!this.$refs.queryBox.contains(e.target)) && (!this.$refs.queryDown.contains(e.target))) {
        this.show = false
      }
    }
  }
}
</script>
<style lang="scss" scoped>
  .global-query {
    float: left;
    .queryBox{
      line-height: 50px;
      font-size: 14px;
      overflow: hidden;
      .search-title {
        float: left;
        &:before {
          content: '';
          display: inline-block;
          width: 18px;
          height: 18px;
          background: url("../assets/redLine/sprite.png") 0 -63px no-repeat;
          background-size: auto;
          vertical-align: middle;
          margin-right: 8px;
        }
      }
      .search-title1 {
        float: left;
      }
      .search-item {
        float: left;
        margin-left: 30px;
      }
      .query{
        margin-left: 20px;
        span {
          padding: 5px 10px;
          background: #57a3f3;
          border-radius: 4px;
          color: #fff;
          margin-left: 10px;
        }
        &:first-child {
          margin-left: 0;
        }
      }
    }
    .queryDown{
      position: absolute;
      top: 55px;
      left: 5px;
      right: 5px;
      z-index: 100;
      border: 1px solid #d1d1d1;
      padding: 30px 20px;
      border-radius: 6px;
      width: calc(100% - 10px);
      background: #fff;
      box-shadow: 3px 3px 5px rgba(0,0,0,.2);
      .ivu-form {
        text-align: left;
        overflow: inherit;
        .ivu-form-item {
          margin-bottom: 0;
        }
        .ivu-btn {
          padding: 5px 10px;
          i {
            vertical-align: middle;
            font-size: 13px;
          }
        }
      }
    }
    &#logQuery {
      .queryDown {
        .ivu-form-item {
          .ivu-form-item-label {
            width: 85px !important;
          }
          .ivu-form-item-content {
            margin-left: 85px !important;
            .ivu-select {
              width: 100px !important;
            }
            .ivu-date-picker, ivu-input-wrapper {
              width: 180px !important;
            }
          }
        }
      }
    }
  }
</style>
